@import "colors";

.contact {
  height: 100vh;
  width: 100%;
  background-image: url(../assets/back-1.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  align-items: center;
  justify-content: center;
  > form {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: white;
    padding: 2rem;
    width: 45%;
    height: 80%;
    justify-content: center;

    > h2 {
      font-size: 1.5rem;
      text-transform: uppercase;
      font-family: "Roboto";
      margin: 2rem 0;
    }
    > textarea {
      resize: none;
    }
    > input,
    > textarea,
    > button {
      padding: 1rem;
      outline: none;
      border: 1px solid rgba(66, 63, 63, 0.864);
      margin: 0.5rem;
      width: 60%;
      border-radius: 6px;
    }
    > button {
      background-color: $cyan;
      border: none;
      cursor: pointer;
      color: white;
      font-size: 1rem;
      &:hover {
        background-color: $darkcyan;
      }
    }
  }
  .FormBorder {
    width: 200px;
    height: 80%;
    background-color: $cyan;
    // border-radius: 0 300px 300px 0;
    position: relative;
    > div {
      width: 85%;
      height: 250px;
      position: absolute;
      top: 50%;
      transform: translate(50%, -50%);
      filter: drop-shadow(0 0 10px rgba(0, 0, 0, 0.8));
      > img {
        width: 160%;
      }
    }
  }
}
@media screen and (max-width: 600px) {
  .contact {
    height: 70vh;
    > form {
      width: 200%;
      height: 100%;
    }
    .FormBorder {
      display: none;
    }
  }
}
