@import "colors";

#catalogue {
  width: 100%;
  min-height: 100vh;
  background-color: white;
  padding: 4rem;
  > h1 {
    font-size: 2rem;
    text-align: center;
    padding: 2rem;
  }
  .card-1 {
    display: flex;
    justify-content: space-evenly;
    margin-bottom: 2rem;
  }
  .card-2 {
    display: flex;
    justify-content: space-evenly;
    margin-bottom: 2rem;
  }
}
.catalogueCard {
  background-color: white;
  width: 30%;
  border-radius: 20px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.495);
  div {
    border-radius: 20px 20px 0 0;
    background-color: $cyan;
    display: flex;
    align-items: center;
    padding: 2rem;
    color: white;
    height: 4rem;
  }
  > main {
    padding: 2rem;
    display: flex;
    flex-direction: column;
    align-items: center;

    > img {
      height: 15rem;
      width: 100%;
      object-fit: contain;
    }
    > h5 {
      font-size: 1.8rem;
    }
    > p {
      text-transform: uppercase;
      letter-spacing: 2px;
      padding: 1rem;
    }
    > button {
      background-color: $cyan;
      color: white;
      border: none;
      padding: 1rem 2rem;
      width: 100%;
      border-radius: 10px;
      cursor: pointer;
      &:hover {
        background-color: $darkcyan;
      }
    }
  }
}
@media screen and (max-width: 600px) {
  #catalogue > div {
    flex-direction: column;
  }
  .catalogueCard {
    width: 100%;
    margin: 1rem 0;
  }
}
